<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>更换头像</span>
    </div>
    <div>
      <!-- 图片，用来展示用户选择的头像 -->
      <img
        v-if="!avatar"
        class="the_img"
        src="../../assets/images/avatar.jpg"
        alt=""
      />
      <img v-else class="the_img" :src="avatar" alt="" />

      <!-- 按钮区域 -->
      <div class="btn-box">
        <input
          type="file"
          accept="image/*"
          style="display: none"
          ref="iptRef"
          @change="onFileChange"
        />
        <el-button type="primary" icon="el-icon-plus" @click="chooseImg"
          >选择图片</el-button
        >
        <el-button
          type="success"
          icon="el-icon-upload"
          :disabled="avatar === ''"
          @click="updateImg"
          >上传头像</el-button
        >
      </div>
    </div>
  </el-card>
</template>

<script>
import { updateUserPictAPI } from '@/api'
export default {
  name: 'UserAvatar',
  data () {
    return {
      avatar: '' // 保存图片链接或者base64字符串
    }
  },
  methods: {
    // 选择图片=>点击事件
    chooseImg () {
      // 目的：让input[type='file']用js代码触发它的点击事件
      this.$refs.iptRef.click()
    },
    // 选择图片确定了
    onFileChange (event) {
      // event原生事件对象
      // 获取用户选择的文件列表(伪数组)
      const files = event.target.files
      if (files.length === 0) {
        this.$message.error('未选择图片')
      } else {
        // 目标：选择的图片文件，要给到前端的img标签预览
        // img标签的src值
        // * 只能是外链地址(http开头，图片文件相对路径)
        // * 或者是图片的base64字符串(只能是data:image/png;base64)
        // 解决方案1： 文件=> 内存临时地址(这个地址只能在js里内存里使用，不能发给后台)
        // 语法： URL.createObjectURL(文件)
        // 返回值：内存临时地址
        // console.log(files[0])
        // this.avatar = URL.createObjectURL(files[0])
        // 解决方案2： 文件=> base64字符串(此字符串是可以发给后台的)
        const fr = new FileReader() // HTML5新出的，读用户选择的文件
        fr.readAsDataURL(files[0]) // 转换为base64
        fr.onload = (e) => {
          // onload等待把文件读成base64字符串后会触发onload事件函数
          this.avatar = e.target.result
          console.log(this.avatar)
        }
      }
    },
    // 调用后端接口，更新头像
    async updateImg () {
      const { data: res } = await updateUserPictAPI(this.avatar)
      console.log(res)
      if (res.code !== 0) {
        return this.$message.error(res.message)
      }
      this.$message.success(res.message)
      // 重新让vuex获取下最新的用户数据
      this.$store.dispatch('getUserInfoActions')
    }
  }
}
</script>

<style lang="less" scoped>
.btn-box {
  margin-top: 10px;
}
.preview {
  object-fit: cover;
}
.the_img {
  width: 350px;
  height: 350px;
}
</style>
